* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.person {
  background: rgb(58, 57, 57);
  color: white;
  width: 17rem;
  padding: 1rem;
  margin: 2rem auto;
  border-radius: 0.3rem;
  text-align: center;
  transition: transform 1s ease-in;
}

img {
  position: relative;
  
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border-radius: 0.1rem solid red;
}

.person h4 {
  text-transform: uppercase;
  margin: 0rem 0;
  letter-spacing: 0.25rem;
  color: white;
}

.person:hover {
  /* transform: scale(1.1);
  transition: transform 1s ease-in; */
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.678);
}

.person-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .person-list {
    display: block;
    justify-content: space-evenly;
  }
}


